const getElement = element => {
  const aElement = element.children.find(x => x.tagName === "a")
  const href = aElement?.properties.href.replace(".md", "").toLowerCase()
  let textElement = aElement?.children.find(x => x.type === "text").value

  if (aElement === undefined) {
    textElement = element.children.find(x => x.type === "text").value
  }

  return {
    href: href,
    textElement: textElement,
    children: [],
  }
}

const DFSUtil = (v, output) => {
  if (v != null) {
    v.visited = true
    if (v.tagName === "ul") {
      if (v.children && v.children.length > 0) {
        for (let i of v.children) {
          if (i.visited === false || i.visited == null) {
            DFSUtil(i, output)
          }
        }
      }
    }
    if (v.tagName === "li") {
      let element = getElement(v)

      const ulSubElement = v.children.find(x => x.tagName === "ul")

      if (ulSubElement) {
        DFSUtil(ulSubElement, element)
      }
      output.children.push(element)
    }
  }
}

const getTocData = htmlAst => {
  let output = { children: [] }

  let htmlAstCopy = JSON.parse(JSON.stringify(htmlAst))
  const list = htmlAstCopy?.children.find(
    x => typeof x === "object" && x.tagName === "ul"
  )
  DFSUtil(list, output)

  return output.children
}

// eslint-disable-next-line no-undef
module.exports.getTocData = getTocData
